.contacto{
    width: 100%;
}

.contacto h1{
    border-bottom: 2px solid  #255fc9;
    width: 70%;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5%;
}

.contacto .info-inicio p{
    width: 80%;
    margin-left: auto;
    margin-right: auto;
}

.contacto .info{
    text-align: left;
    width: max-content;
    margin-left: auto;
    margin-right: auto;
}

.contacto .info-contacto .icono{
    font-size: 1.2em;
    margin-right: 10px;
}