header{
    background-color:white;
    color: #064ACB;
    padding: 10px 0px 25px 20px;
    height: 70px;
    border-bottom: #255fc9 2px solid;
}

header .logo{
    float:left;
    width: 90px;
    height: 90px;
}

.iconos{
    padding-top: 15px;
    color:#064ACB;
    font-size: 2em;
    display: none;
}

.barra-menu{
    margin-top: 10px;
    float: right;
    margin-right: 20px;
}
.desplegable{
    float:right;
    margin-right: 20px;
}

.barra-menu .enlace{
    height: 60px;
    padding-left: 20px;
    padding-right: 20px;
    display:inline-block;
    font-size: 1.2em;
    margin-left: 20px;
    color: #064ACB;
    text-decoration: none;
    line-height: 55px;
    border-radius: 12px;
}

.enlace:hover{
    background-color:#255fc9;
    color:white;
}

@media screen and (max-width:1024px) {
    header{
        padding: 0px;
        padding-bottom: 25px;
    }
    .iconos{
        display: block;
        padding-left: 20px;
        padding-top: 30px;
    }
    .barra-menu{
        display:none;
    }
    .barra-menu-activa{
        display:block;
        position:absolute;
        z-index: 7;
        width: 100%;
        background:#064bcb85;
        height: max-content;
        margin-top: 97px;
        text-align: center;
        border-top: 2px solid white;
    }
    .enlace{
        width:80%;
        display:block;
        font-size: 1.2em;
        padding-top:15px;
        padding-bottom:15px;
        margin-left: auto;
        margin-right: auto;
        color:white;
        text-align: center;
        text-decoration: none;
        line-height: 55px;
        border-bottom: 2px solid white;
    }
}