footer {
    background-color: #303030;
    text-align: center;
    color: white;
    padding-top: 30px;
    padding-bottom: 10px;
    margin-top: 60px;
    width: 100%;
}

footer .contenedor_footer {
    display: flex;
    justify-content: center;
    margin-top: 50px;
    margin: 30px 0px 0px 0px;
    padding-bottom: 20px;
}

footer .contenedor_footer .contenedor_pie {
    text-align: left;
    display: inline-block;
    margin-left: 40px;
    margin-right: 40px;
    color: #676767;
}

footer .contenedor_footer .contenedor_pie .enlace_pie {
    /* width: 350px; */
    display: block;
    margin-top: 10px;
}

footer .contenedor_footer .contenedor_pie h4 {
    color: white;
}

footer .contenedor_footer .contenedor_pie a {
    text-decoration: none;
    color: #676767;
}

footer .contenedor_footer .contenedor_pie .iconos-rs {
    margin-right: 20px;
    font-size: 3em;
}

footer .imagen-pie {
    display: block;
}

footer .copy{
    width: 90%;
    margin-left: auto;
    margin-right: auto;
}

footer .copy a{
    text-decoration: none;
    font-weight: bold;
    color: white;
}

/*
Tamaño de mediano tablet
*/

@media screen and (min-width:768px) and (max-width:1024px) {
    footer {
        width: 100%;
    }
    footer .contenedor_footer .contenedor_pie p {
        width: 200px;
    }
    footer .contenedor_footer .contenedor_pie {
        text-align: left;
        display: inline-block;
        margin-left: 15px;
        margin-right: 15px;
        color: #676767;
    }
    footer .contenedor_footer .contenedor_pie h4 {
        margin-bottom: 10px;
    }
}

/*
Tamaño de pequeño de movil
*/

@media screen and (max-width:767px) {
    footer {
        width: 100%;
    }
    footer h2 {
        width: 95%;
    }
    footer .contenedor_footer {
        display: block;
        margin-bottom: 10px;
    }
    footer .contenedor_footer .contenedor_pie p {
        width: 90%;
    }
    footer .contenedor_footer .contenedor_pie {
        display: block;
        margin-top: 15px;
    }
    footer .contenedor_footer .contenedor_pie h4 {
        margin-bottom: 10px;
    }
    #logo_pie {
        width: 30%;
        height: 30%;
    }
}