.contenedor-servicios{
    width: 350px;
    display:inline-block;
    border-radius:  15px;
    -webkit-box-shadow: 0px 3px 20px -2px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 3px 20px -2px rgba(0,0,0,0.75);
    box-shadow: 0px 3px 20px -2px rgba(0,0,0,0.75);
    margin-left: 15px;
    margin-right: 15px;
    padding: 20px 0px 20px 0px;
}

.contenedor-servicios:hover{
    transform: scale(1.02);
}

.contenedor-servicios .c-icono .icono{
    font-size: 6em;
}

@media screen and (max-width:1024px) {
    .contenedor-servicios{
        margin-left: auto;
        margin-right: auto;
        margin-top: 20px;
        margin-bottom: 20px;
    }
}