.principal .img-portada{
    width: 100%;
    height: 80vh;
    object-fit: cover;
}

.principal section{
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    margin-bottom: 50px;
}

.principal section p{
    width: 80%;
    margin-left: auto;
    margin-right: auto;
}

.principal section .titulo-destacado{
    font-size: 1.2em;
    font-weight: bold;
    width: 50%;
    height: max-content;
    padding: 10px 10px 10px 10px;
    border-radius: 10px;
    background-color: #255fc9;
    color:white;
    margin-left: auto;
    margin-right: auto;
    text-decoration: none;
}

.principal .contenedor-3 {
    padding: 90px 0px 90px 0px;
    background-color: #366ed8;
}

.principal .contenedor-3 .tarjeta {
    width: 70%;
    display: table;
    margin: 0 auto;
    margin-left: auto;
    margin-right: auto;
    padding: 16px;
    border-radius: 5px;
    background-color: #ffffff;
    box-shadow: 0px 0px 15px -9px rgba(0, 0, 0, 0.75);
}

.principal .contenedor-3 .tarjeta .contenedor-des,
.principal .contenedor-3 .tarjeta .contenedor-enlace
{
    margin-bottom: 15px;
    margin-top: 30px;
}

.principal .contenedor-3 .tarjeta  .titulo-destacado{
    font-size: 1.1em;
    font-weight: bold;
    width: 50%;
    padding: 10px 10px 10px 10px;
    border-radius: 10px;
    background-color: #255fc9;
    color:white;
    margin-left: auto;
    margin-bottom: 20px;
    margin-top: 15px;
    margin-right: auto;
    text-decoration: none;
}

@media screen and ( max-width: 1000px) {
    .principal .contenedor-3 .tarjeta{
        width: 80%;
    }
}
